import React from 'react'
import Box from '@material-ui/core/Box'
import theme from '../styles/theme'

import DirectoryBox from '../components/DirectoryBox'

export const post_query = graphql`
    query ($slug: String!) {
      mdx(frontmatter: {draft: {ne: true} },
        fields: { slug: { eq: $slug }}){
        ...postFieldsBody
               
      }
    }

`

export default function OgTemplate({ data, pageContext }) {
    const node = data.mdx
    //const light = theme.palette.primary.light
    const dark = theme.palette.primary.dark
    
    return (
        <div >
            <style jsx global>{`
                body { 
                    color: ${theme.palette.primary.contrastText};
                    background-color: ${dark};
                }
            `}
            </style>
            <Box p={4}>
            <span>{node.frontmatter.date}</span>
            <DirectoryBox directory={node.fields.directory}/>
            <h1>{node.fields.postTitle}</h1>
            <div>{node.frontmatter.description || node.excerpt}</div>
            </Box>
        </div>
    )
}